.tab {
    overflow: hidden;
    border: 1px solid #ccc;
    background-color: #D9D9D9;
    margin:3% 3% 0 3% ;
    display: inline-flex;
    box-shadow: 0 2px 5px rgba(0, 0, 0, .26);
    border-radius: 10px;
  }
  
  /* Style the buttons inside the tab */
.tab button {
    background-color: white;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    /* padding: 14px 16px; */
    height: 20%;
    transition: 0.3s;
    font-size: 3.5vw;

  }
  
  /* Change background color of buttons on hover */
  .tab button:hover {
    background-color:#D9D9D9;
  }
  
  /* Create an active/current tablink class */
  .tab button.active {
    background-color:#9c9c9cb2;
  }
  
  /* Style the tab content */
  .tabcontent {
    border: none;
    display: none;
    padding: 6px 12px;
    width: 70%;
    padding: 3%;
    margin: auto;
  }

